
<template>
  <div class="bg-gray-100">
    <div id="wrapper" class="flex flex-col justify-between h-screen">
      <!-- header-->
      <subHeader ref="subHeader"></subHeader>
      <!-- Content-->

      <div>
        <div
          class="lg:p-12 max-w-md max-w-xl lg:my-0 my-12 mx-auto p-6 space-y-"
        >
          <h1 class="lg:text-3xl text-xl font-semibold mb-6">注 册</h1>
          <p class="mb-2 text-black text-lg">注册填写您的账户信息</p>
          <form action="">
            <div class="flex lg:flex-row flex-col lg:space-x-2">
              <input
                type="text"
                placeholder="First Name"
                class="bg-gray-200 mb-2 shadow-none dark:bg-gray-800"
                style="border: 1px solid #d3d5d8 !important"
              />
              <input
                type="text"
                placeholder="Last Name"
                class="bg-gray-200 mb-2 shadow-none dark:bg-gray-800"
                style="border: 1px solid #d3d5d8 !important"
              />
            </div>
            <input
              type="text"
              placeholder="Email"
              class="bg-gray-200 mb-2 shadow-none dark:bg-gray-800"
              style="border: 1px solid #d3d5d8 !important"
            />
            <input
              type="text"
              placeholder="Password"
              class="bg-gray-200 mb-2 shadow-none dark:bg-gray-800"
              style="border: 1px solid #d3d5d8 !important"
            />
            <input
              type="text"
              placeholder="Confirm Password"
              class="bg-gray-200 mb-2 shadow-none dark:bg-gray-800"
              style="border: 1px solid #d3d5d8 !important"
            />
            <div class="flex justify-start my-4 space-x-1">
              <div class="checkbox">
                <input type="checkbox" id="chekcbox1" checked />
                <label for="chekcbox1"
                  ><span class="checkbox-icon"></span> 我同意</label
                >
              </div>
              <a href="#" style="margin-top:2px;color:#3A79D8"> 条款和协议</a>
            </div>
            <button
              type="submit"
              class="bg-gradient-to-br from-pink-500 py-3 rounded-md text-white text-xl to-red-400 w-full"
            >
              Login
            </button>
            <div class="text-center mt-5 space-x-2">
              <p class="text-base">
                您有账户? <a href="#" @click="toLogin()" style="color:#3A79D8"> 登录 </a>
              </p>
            </div>
          </form>
        </div>
      </div>

      <!-- Footer -->
      <subFooter></subFooter>
    </div>
  </div>
</template>

<script>
import subHeader from "@/components/subHeader";
import subFooter from "@/components/subFooter";
export default {
  name: "register",
  components: { subHeader, subFooter },
  data() {
    return {};
  },
  methods: {
    toLogin() {
      this.$router.push({ name: "login" });
    },
  },
  mounted() {
    this.$refs.subHeader.isActiveLogin = false;
    this.$refs.subHeader.isActiveRegister = true;
  },
};
</script>

<style lang="scss" scoped>
</style>



